.Banner-background {
    width:100%;
    height:100vh;
    background: #0a2247;
}

.Banner-heading {
    color: rgba(255,255,255,0.8);
    font-family: 'Roboto Condensed', 'Lucida Sans Typewriter', 'Arial';
    font-size: 3vw;
    margin-left: 1em;
    top: 30vh;
    position: absolute;
    animation: mymove 0.8s infinite;
    animation-direction: alternate-reverse;
}

.Banner-subheading {
    font-size: 2vw !important;
    color: rgba(255,255,255,0.5);

}

.Banner-icon ul {
    list-style: none;
}

.Banner-icon ul li {
    display: inline;
    font-size:2vw;
    margin-right: 1em;
    color: rgba(255,255,255,0.5);

}
  
    












@keyframes mymove {
    from {top: 30vh;}
    to {top: 32vh;}
}

@media only screen and (max-width: 600px) {
    .Banner-heading {
        font-size: 9vw;
    }
    .Banner-icon ul li {
        display: inline;
        font-size:5vw;
        margin-right: 0.5em;
    }
    .Banner-subheading {
        font-size: 5vw !important;
    }
    
}